body {
  color: #fff;
  background-color: #333;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

h1 {
  margin-bottom: 20px;
}

.board {
  grid-template-rows: repeat(3, 100px);
  grid-template-columns: repeat(3, 100px);
  gap: 5px;
  display: grid;
}

.cell {
  cursor: pointer;
  color: #333;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 2rem;
  display: flex;
}

.cell.x {
  color: #fff;
  background-color: red;
}

.cell.o {
  color: #fff;
  background-color: #00f;
}

.message {
  background-color: #000c;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.message.show {
  display: flex;
}

button {
  cursor: pointer;
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 1rem;
}
